import React, { Fragment, memo } from "react";
import { Loading } from "../Loading";

type Props = {
  name: string;
  type?: string;
  messError?: string;
  className?: string;
  progress?: number;
  image?: string;
  trash?: boolean;
  onChange?: (e: React.ChangeEvent<HTMLInputElement>) => void;
  onDeleteImage: () => void;
};

const ImageUpload = ({
  name,
  progress = 0,
  onDeleteImage,
  messError,
  image = "",
  className,
  trash,
  ...props
}: Props) => {
  return (
    <div className="image-upload">
      <label className={`image-upload-label ${className}`}>
        <input name={name} className="hidden-input" {...props} />
        {!image && progress === 0 && (
          <div className="image-default ">
            <img src="/img/img-upload.png" alt="upload-img" className="" />
            {!!messError ? (
              <span className="error-massage">
                <i className="fa-solid fa-circle-exclamation"></i> {messError}
              </span>
            ) : (
              <p>Choose photo</p>
            )}
          </div>
        )}

        {progress !== 0 && !image && <Loading className="img" />}
        {image && (
          <Fragment>
            <img src={image} className="immage-load" alt="" />
            {trash && (
              <button
                type="button"
                className="btn-trash"
                onClick={onDeleteImage}
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24"
                  strokeWidth="1.5"
                  stroke="currentColor"
                  className="w-6 h-6"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    d="M14.74 9l-.346 9m-4.788 0L9.26 9m9.968-3.21c.342.052.682.107 1.022.166m-1.022-.165L18.16 19.673a2.25 2.25 0 01-2.244 2.077H8.084a2.25 2.25 0 01-2.244-2.077L4.772 5.79m14.456 0a48.108 48.108 0 00-3.478-.397m-12 .562c.34-.059.68-.114 1.022-.165m0 0a48.11 48.11 0 013.478-.397m7.5 0v-.916c0-1.18-.91-2.164-2.09-2.201a51.964 51.964 0 00-3.32 0c-1.18.037-2.09 1.022-2.09 2.201v.916m7.5 0a48.667 48.667 0 00-7.5 0"
                  />
                </svg>
              </button>
            )}
          </Fragment>
        )}
        {!image && (
          <div
            className="not-image"
            style={{
              width: `${Math.ceil(progress)}%`,
            }}
          ></div>
        )}
      </label>
    </div>
  );
};

export default memo(ImageUpload);
